/** Media Queries **/
html {
  padding: 0;
  margin: 0;
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Outfit", sans-serif;
  font-size: 1.8rem;
  background-color: rgb(224, 255, 247);
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

.main-container {
  width: min(95%, 60rem);
  margin: 0 auto;
  margin: 2rem auto;
  border-radius: 1rem;
  box-shadow: 0 0 10px 1px rgb(130, 130, 130);
  background-color: white;
  padding: 2rem;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.text-center {
  text-align: center;
}

.login__heading {
  color: white;
  font-size: 4rem;
  text-align: center;
}

.formulario {
  background-color: white;
}
.formulario__campo {
  margin: 1rem 0;
}
.formulario__campo--select {
  display: grid;
  gap: 0rem;
  grid-template-columns: repeat(2, 1fr);
  grid-template-columns: 25% 75%;
  align-items: center;
}
.formulario__input {
  width: 100%;
  height: 4rem;
  padding: 0 1rem;
}
.formulario__select {
  height: 4rem;
  padding: 0 1.5rem;
  border: 1px solid black;
}
.formulario__select--azul {
  background: rgb(43, 117, 255);
  color: white;
}
.formulario__select--morado {
  background-color: rgb(129, 69, 226);
  color: white;
}
.formulario__submit {
  background-color: green;
  padding: 1.5rem 4rem;
  color: white;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  width: 100%;
  margin-top: 1.5rem;
}
.formulario__submit:hover {
  background-color: #004d00;
  cursor: pointer;
}

.formulario-google {
  display: flex;
  flex-direction: column;
}
.formulario-google__campo {
  width: 100%;
  margin: 0.5rem 0;
}
.formulario-google__campo {
  position: relative;
  margin: 1rem 0;
}
.formulario-google__campo input {
  background-color: transparent;
  color: white;
  width: 100%;
  height: 6rem;
  border-radius: 1rem;
  border: 1px solid #2C7873;
  font-size: 2rem;
  padding: 0 2rem;
}
.formulario-google__campo label {
  position: absolute;
  left: 22px;
  top: 17px;
  color: #2C7873;
  font-size: 2rem;
  transition: color 0.3s ease, transform 0.3s ease;
  user-select: none;
}
.formulario-google__campo input:focus {
  border: 2px solid white;
  outline: none;
}
.formulario-google__campo input:focus ~ label,
.formulario-google__campo input:valid ~ label {
  text-align: center;
  padding: 0 0.8rem;
  height: 25px;
  background-color: #021C1E;
  color: white;
  transform: translateY(-29px) translateX(-10px) scale(0.8);
  border-radius: 3px;
}
.formulario-google__submit {
  background-color: #6FB98F;
  padding: 1.5rem 4rem;
  color: #021C1E;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  border-radius: 5px;
  margin-bottom: 3rem;
  margin-top: 1rem;
}
.formulario-google__submit:hover {
  background-color: #50a575;
  cursor: pointer;
}

.formulario--search__container {
  position: relative;
}
.formulario--search__input {
  padding: 0.5rem 1rem;
  width: 70%;
}
.formulario--search__submit {
  height: 3.7rem;
  width: 30%;
  position: absolute;
  border: none;
  background-color: rgb(0, 0, 0);
  color: white;
}

.alerta {
  color: white;
  border-radius: 3px;
  padding: 0.5rem 1rem;
  margin-bottom: 1rem;
  font-size: 1.6rem;
}
.alerta__error {
  background-color: rgb(110, 0, 0);
  border: 2px solid red;
}
.alerta__exito {
  background-color: rgb(0, 90, 0);
  border: 2px solid rgb(19, 175, 5);
}

.header {
  margin: 0;
  width: 100%;
}
.header__time {
  background-color: rgb(92, 9, 131);
  text-align: center;
}
.header__date {
  color: white;
  margin: 0;
  padding: 0.5rem 0;
  font-size: 1.5rem;
  display: inline-block;
}
.header__date--time {
  margin-left: 1rem;
  font-weight: 700;
}
.header__banner {
  background-color: #021C1E;
  text-align: center;
}
.header__logo {
  padding: 2.4rem 0;
  margin: 0;
  color: white;
  font-size: 2.4rem;
}
.header__section {
  background-color: #2C7873;
}
.header__texto {
  color: white;
  margin: 0;
  padding: 0.3rem 0 0.5rem 0;
  text-align: center;
}

.tabla--lineal {
  width: 100%;
  border-spacing: 0;
  border-bottom: 3px solid #021C1E;
  border-top: 3px solid #021C1E;
}
.tabla--lineal__tr:nth-child(odd) {
  background-color: white;
}
.tabla--lineal__tr:nth-child(even) {
  background-color: rgb(230, 230, 230);
}
.tabla--lineal__th {
  background: #021C1E;
  color: white;
  width: 2rem;
  border-bottom: 1px solid white;
}
.tabla--lineal__tr:last-child .tabla--lineal__th {
  border-bottom: none;
}
.tabla--lineal__td {
  font-size: 1.4rem;
  border: 1px solid black;
  padding: 0.5rem;
}
.tabla--lineal__td p {
  margin: 0;
}
.tabla--lineal__td span {
  font-weight: bold;
}
.tabla--lineal__td a {
  padding: 1rem;
  text-align: center;
  display: block;
}
.tabla--lineal__informacion {
  display: grid;
  gap: 0rem;
  grid-template-columns: repeat(2, 1fr);
  text-align: center;
  font-size: 1.3rem;
  margin-top: 0.8rem;
}
.tabla--lineal__informacion p:nth-child(3), .tabla--lineal__informacion p:nth-child(4) {
  margin-top: 0.4rem;
}
.tabla--lineal__acciones {
  width: 40%;
}
.tabla--lineal__acciones--datos {
  background-color: rgb(120, 8, 172);
  color: white;
  font-weight: 700;
  border-style: none;
  border-radius: 0.5rem;
  margin: 0 auto 0.5rem auto;
  width: 100%;
  padding: 0.9rem 0;
}
.tabla--lineal__acciones--venta {
  background-color: rgb(0, 128, 11);
  color: white;
  font-weight: 700;
  border-style: none;
  border-radius: 0.5rem;
  margin: 0 auto;
  width: 100%;
  padding: 0.9rem 0;
}

.auth {
  background-color: rgb(2, 7, 27);
}
.auth__contenido {
  width: min(95%, 60rem);
  margin: 0 auto;
  background-color: #021C1E;
  border-radius: 2rem;
  border: 3px solid #2C7873;
  margin: 8rem auto;
  padding: 1rem 2rem;
  box-shadow: 0 0 50px 10px rgb(9, 9, 80);
}

.menu {
  text-align: center;
}
.menu__logout-button {
  background-color: rgb(40, 40, 40);
  padding: 1.5rem 4rem;
  color: white;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  width: 20rem;
  border-radius: 0.5rem;
  margin-bottom: 3.5rem;
}
.menu__logout-button:hover {
  background-color: #0f0f0f;
  cursor: pointer;
}

.pill {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
  padding: 4rem;
  justify-content: center;
}
.pill__container {
  border-radius: 0.5rem;
  color: white;
  text-align: center;
  padding: 1.5rem 0;
}
.pill__container:nth-child(1) {
  background-color: rgb(223, 70, 0);
}
.pill__container:nth-child(2) {
  background-color: rgb(12, 129, 184);
}
.pill__container:nth-child(3) {
  background-color: rgb(96, 8, 197);
}
.pill__container:nth-child(4) {
  background-color: rgb(0, 158, 13);
}
.pill__container:nth-child(5) {
  background-color: rgb(90, 90, 90);
}
.pill__container:nth-child(6) {
  background-color: rgb(184, 1, 1);
}
.pill__container:nth-child(7) {
  background-color: rgb(19, 110, 106);
}
.pill__container:nth-child(8) {
  background-color: rgb(110, 11, 94);
}
.pill__logo {
  font-size: 2.5rem;
}
.pill__texto {
  font-size: 1.4rem;
  margin: 0.5rem 0 0 0;
}

.cliente-nuevo {
  background-color: white;
  border-radius: 1rem;
}

.button-container {
  display: flex;
  margin-bottom: 1rem;
}
.button-container__back {
  background-color: rgb(43, 117, 255);
  color: white;
  padding: 1rem;
  border-radius: 0.5rem;
  margin-left: auto;
  font-size: 1.6rem;
}